body{
  background: rgb(172,232,238);
background: linear-gradient(90deg, rgba(172,232,238,0.8) 0%, rgba(231,168,227,0.8) 100%);

 
}
.App {
  text-align: center;
  font-family: arial;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.menu {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  background: rgb(63,94,251);
  background: radial-gradient(circle, rgba(63,94,251,0.77) 0%, rgba(252,70,107,0.7679446778711485) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border-style: solid;
}

.menu input, button {
  width: 300px;
  height: 50px;
  margin: 5px;
  
  border-radius: 5px;
  font-size: 20px;
  cursor:pointer;
}

.quiz {
  width: 500px;
  height: 500px;
  border-radius: 10px;
